$watch-size: 400px;
$height-second-hand: $watch-size / 2.2;
$height-minute-hand: $watch-size / 3;
$second-hand-position: $watch-size / 2 - $height-second-hand;
$minute-hand-position: $watch-size / 2 - $height-minute-hand;
$center-point: $watch-size * 0.05;
$position-num: $watch-size / 2 - font-size;
